import common from '../../common/common.js';
export default {
	data() {
		return {
			globalUser: {}
		};
	},
	onShow() {
		var me = this;
		var globalUser = me.getGlobalUser('globalUser');
		me.globalUser = globalUser;
		//debugger;
	},
	methods: {
		operator(){
			var me =this;
			var globalUser = me.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList:["查看我的头像","从相册上传"],
				success(res) {
					var index =res.tapIndex;
					if(index ==0){
						var faceArr =[]
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls:faceArr,
							current:faceArr[0]
							
							
						})
					}else if( index==1){
						uni.chooseImage({
							count:1,
							sizeType: ['original', 'compressed'],
							sourceType: ['album'],
							success(res){
								
								//获得临时路径
								var tempFilePaths = res.tempFilePaths[0];
								uni.navigateTo({
									url:"../meFace/meFace?tempFilePaths" + tempFilePaths
								})
							}
						})
						
						
					}
					;
					
				}
			})
			
		},
		
		
		
		
		
		cleanStorage() {
			uni.remoStorageSync();
			//uni.cleanStorageSync();
			uni.showToast({
				title: '清理缓存成功',
				mask: false,
				duration: 1500
			});
		},
		
		
		
		logout() {
		
			var serverUrl = common.serverUrl;
			var globalUser = this.getGlobalUser('globalUser');
			

			uni.request({
				url: serverUrl + '/user/logout?qq=&userId=' + globalUser.id, //仅为示例，并非真实接口地址。
				method: 'POST',
				success: res => {
					// console.log(res.data);
					//获取真实数据前,判断状态是否为200
					if (res.data.status == 200) {
						//debugger;
						uni.removeStorageSync('globalUser');
						uni.switchTab({
							url: '../me/me'
						});
					}
				}
			});
		}
	}
};
